module.exports = " <div class=jumbotron _v-3846b602=\"\"> <div class=container _v-3846b602=\"\"> <h1 _v-3846b602=\"\"><span class=\"glyphicon glyphicon-wrench\" _v-3846b602=\"\"></span>勞基法計算機</h1> <p _v-3846b602=\"\"> 沒人搞得清楚這次修法正確的計算方式，不如來個計算機自己按一按吧。 </p> </div> </div> <div class=container _v-3846b602=\"\"> <div class=\"alert alert-info\" _v-3846b602=\"\"> <span class=\"glyphicon glyphicon-pencil\" aria-hidden=true _v-3846b602=\"\"></span> <span _v-3846b602=\"\"> 前置條件： <ul _v-3846b602=\"\"> <li _v-3846b602=\"\"><a target=_blank href=\"http://laws.mol.gov.tw/Chi/FLAW/FLAWDOC03.asp?keyword=&amp;lc1=FL014930%2C+20150701%2C+24&amp;sdate=&amp;edate=&amp;datatype=etype&amp;recordNo=7\" _v-3846b602=\"\">勞動 2 字第 0960130677 號 函</a>：原約定月薪給付總額相當於 240 小時者（即「平日每小時工資額」係以月薪總額除以 30 再除以 8 核計者）</li> <li _v-3846b602=\"\">現行勞基法與一例一休的例假日假設為週日，兩例的例假日為週六與週日</li> <li _v-3846b602=\"\">現行勞基法的週六假設為約定不用上班的日子</li> <li _v-3846b602=\"\">一例一休的休息日假設為星期六</li> <li _v-3846b602=\"\">基於以上假設，在計算輪班制度時（例如四班二輪）可能會與實際狀況有誤差</li> <li _v-3846b602=\"\">下面的「額外工資」欄位包含加班費與例假日上班的工資加倍發給</li> </ul> </span> </div> <h2 _v-3846b602=\"\">條件設定</h2> <p _v-3846b602=\"\">假設勞工採月薪制，其月薪 <input class=monthly-pay type=number v-model=monthlyPay _v-3846b602=\"\"> 元，每月總工時為 {{assumingWorkHours}} 計算，平均時薪為 {{hourlyPay}} 元。</p> <p class=dayoff-options _v-3846b602=\"\"> 例假日工作條件設定：<br _v-3846b602=\"\"> <label _v-3846b602=\"\"><input type=checkbox v-model=disaster _v-3846b602=\"\"> 例假日發生了天災、事變或突發事件</label><br _v-3846b602=\"\"> <label _v-3846b602=\"\"><input type=checkbox v-model=laborAgree _v-3846b602=\"\"> 勞工同意例假日加班</label> </p> <div class=input _v-3846b602=\"\"> <label _v-3846b602=\"\">週一 <input debounce=100 type=number min=0 max=24 class=workhours v-model=workhours[0] _v-3846b602=\"\"></label> <label _v-3846b602=\"\">週二 <input debounce=100 type=number min=0 max=24 class=workhours v-model=workhours[1] _v-3846b602=\"\"></label> <label _v-3846b602=\"\">週三 <input debounce=100 type=number min=0 max=24 class=workhours v-model=workhours[2] _v-3846b602=\"\"></label> <label _v-3846b602=\"\">週四 <input debounce=100 type=number min=0 max=24 class=workhours v-model=workhours[3] _v-3846b602=\"\"></label> <label _v-3846b602=\"\">週五 <input debounce=100 type=number min=0 max=24 class=workhours v-model=workhours[4] _v-3846b602=\"\"></label> <label _v-3846b602=\"\">週六 <input debounce=100 type=number min=0 max=24 class=workhours v-model=workhours[5] _v-3846b602=\"\"></label> <label _v-3846b602=\"\">週日 <input debounce=100 type=number min=0 max=24 class=workhours v-model=workhours[6] _v-3846b602=\"\"></label> </div> <div class=row _v-3846b602=\"\"> <div class=col-md-4 _v-3846b602=\"\"> <h3 _v-3846b602=\"\">勞基法現行版本</h3> <ul _v-3846b602=\"\"> <li _v-3846b602=\"\">週薪：{{regularPay}} 元</li> <li _v-3846b602=\"\">額外工資：{{currentSolution.overtimePay}} 元</li> <li _v-3846b602=\"\">總計週薪：{{regularPay + currentSolution.overtimePay}} 元</li> <li _v-3846b602=\"\">工時：{{totalWorkHours}}</li> <li v-if=\"workhours[6] > 0 &amp;&amp; laborAgree &amp;&amp; disaster\" class=info _v-3846b602=\"\">額外補休時數：1 日</li> <li class=warning v-show=\"workhours[6] > 0 &amp;&amp; !disaster &amp;&amp; laborAgree\" _v-3846b602=\"\"> <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=40\" _v-3846b602=\"\">違法</a>：非天災、事變或突發事件禁止於 <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=36\" _v-3846b602=\"\">例假日（週日）</a> 工作， <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=79\" _v-3846b602=\"\">違者處 2 萬以上 30 萬以下罰鍰</a> 。 </li> </ul> <table class=week _v-3846b602=\"\"> <tbody _v-3846b602=\"\"><tr _v-3846b602=\"\"><th v-for=\"name in daynames\" _v-3846b602=\"\"> {{ name }} </th><th _v-3846b602=\"\"> </th></tr><tr v-for=\"hour in currentSolution.transposed\" _v-3846b602=\"\"> <td v-for=\"day in hour\" track-by=$index _v-3846b602=\"\"> <span v-if=\"day !== 0\" class=emoji v-bind:class=\"{\n              'regular': day === 1,\n              'overtime': day === 2,\n              'overtime-two-hours': day === 3,\n              'work-on-dayoff': day === 4,\n              'off': day === 0\n            }\" _v-3846b602=\"\"></span> <span class=emoji v-if=\"day === 0\" _v-3846b602=\"\">--</span> </td> </tr> </tbody></table> <div class=\"alert alert-info\" _v-3846b602=\"\"> 相關規定： <ul _v-3846b602=\"\"> <li _v-3846b602=\"\">台(87)勞動二字第39675號函：例假日（通常是週日）上班低於八個小時，薪水均為 {{hourlyPay}} x 8</li> </ul> </div> </div> <div class=col-md-4 _v-3846b602=\"\"> <h3 _v-3846b602=\"\">勞動部草案一例一休版本</h3> <ul _v-3846b602=\"\"> <li _v-3846b602=\"\">週薪：{{regularPay}} 元</li> <li _v-3846b602=\"\">額外工資：{{oneRestOneOffSolution.overtimePay}} 元</li> <li _v-3846b602=\"\">總計週薪：{{regularPay + oneRestOneOffSolution.overtimePay}} 元</li> <li _v-3846b602=\"\">工時：{{totalWorkHours}}</li> <li v-if=\"workhours[6] > 0 &amp;&amp; laborAgree &amp;&amp; disaster\" class=info _v-3846b602=\"\">額外補休時數：1 日</li> <li class=warning v-show=\"workhours[6] > 0 &amp;&amp; !disaster &amp;&amp; laborAgree\" _v-3846b602=\"\"> <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=40\" _v-3846b602=\"\">違法</a>：非天災、事變或突發事件禁止於 <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=36\" _v-3846b602=\"\">例假日（週日）</a> 工作， <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=79\" _v-3846b602=\"\">違者處 2 萬以上 30 萬以下罰鍰</a> 。 </li> </ul> <table class=week _v-3846b602=\"\"> <tbody _v-3846b602=\"\"><tr _v-3846b602=\"\"><th v-for=\"name in daynames\" _v-3846b602=\"\"> {{ name }} </th><th _v-3846b602=\"\"> </th></tr><tr v-for=\"hour in oneRestOneOffSolution.transposed\" _v-3846b602=\"\"> <td v-for=\"day in hour\" track-by=$index _v-3846b602=\"\"> <span v-if=\"day !== 0\" class=emoji v-bind:class=\"{\n              'regular': day === 1,\n              'overtime': day === 2,\n              'overtime-two-hours': day === 3,\n              'work-on-dayoff': day === 4,\n              'off': day === 0\n            }\" _v-3846b602=\"\"></span> <span class=emoji v-if=\"day === 0\" _v-3846b602=\"\">--</span> </td> </tr> </tbody></table> <div class=\"alert alert-info\" _v-3846b602=\"\"> 相關規則： <ul _v-3846b602=\"\"> <li _v-3846b602=\"\"> 根據 <a href=http://www.cna.com.tw/news/firstnews/201606290106-1.aspx target=_blank _v-3846b602=\"\">報導</a>，休息日工資計算的部分，擬從原本的加倍發給，改為在2小時以內者，按平日工資額另給予每小時1又1/3，再繼續工作者另給予每小時1又2/3。 </li> <li _v-3846b602=\"\">工作時間計算方式為工作4小時以內，以4小時計算，超過4小時至8小時，以8小時計算，超過8小時至12小時以內者，以12小時計。</li> </ul> </div> </div> <div class=col-md-4 _v-3846b602=\"\"> <h3 _v-3846b602=\"\">一週兩例假日版本</h3> <ul _v-3846b602=\"\"> <li _v-3846b602=\"\">週薪：{{regularPay}} 元</li> <li _v-3846b602=\"\">額外工資：{{twoOffSolution.overtimePay}} 元</li> <li _v-3846b602=\"\">總計週薪：{{regularPay + twoOffSolution.overtimePay}} 元</li> <li _v-3846b602=\"\">工時：{{totalWorkHours}}</li> <li v-if=\"(workhours[6] > 0 || workhours[5] > 0)  &amp;&amp; laborAgree &amp;&amp; disaster\" class=info _v-3846b602=\"\"> 額外補休時數：{{ (workhours[6] &gt; 0 ? 1 : 0) + (workhours[5] &gt; 0 ? 1 : 0) }} 日 </li> <li class=warning v-show=\"(workhours[6] > 0 || workhours[5] > 0) &amp;&amp; !disaster &amp;&amp; laborAgree\" _v-3846b602=\"\"> <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=40\" _v-3846b602=\"\">違法</a>：非天災、事變或突發事件禁止於 <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=36\" _v-3846b602=\"\">例假日（週六與週日）</a> 工作， <a target=_blank href=\"http://law.moj.gov.tw/LawClass/LawSingle.aspx?Pcode=N0030001&amp;FLNO=79\" _v-3846b602=\"\">違者處 2 萬以上 30 萬以下罰鍰</a> 。 </li> </ul> <table class=week _v-3846b602=\"\"> <tbody _v-3846b602=\"\"><tr _v-3846b602=\"\"><th v-for=\"name in daynames\" _v-3846b602=\"\"> {{ name }} </th><th _v-3846b602=\"\"> </th></tr><tr v-for=\"hour in twoOffSolution.transposed\" _v-3846b602=\"\"> <td v-for=\"day in hour\" track-by=$index _v-3846b602=\"\"> <span v-if=\"day !== 0\" class=emoji v-bind:class=\"{\n              'regular': day === 1,\n              'overtime': day === 2,\n              'overtime-two-hours': day === 3,\n              'work-on-dayoff': day === 4,\n              'off': day === 0\n            }\" _v-3846b602=\"\"></span> <span class=emoji v-if=\"day === 0\" _v-3846b602=\"\">--</span> </td> </tr> </tbody></table> <div class=\"alert alert-info\" _v-3846b602=\"\"> 相關規則： <ul _v-3846b602=\"\"> <li _v-3846b602=\"\">國民黨版在立法說明提到比照公務員，兩例有可能實質變兩休，但目前草案並沒有這個效果，還要觀察是否有後續動作</li> </ul> </div> </div> </div> </div> <div class=footer _v-3846b602=\"\"> 本專案源碼於 <a href=https://github.com/g0v/workweek _v-3846b602=\"\">g0v/workweek</a> 以 MIT 授權釋出， 有任何問題請提交至 <a href=https://github.com/g0v/workweek/issues _v-3846b602=\"\">issue tracker</a> </div> ";


